import axios from 'axios';
import PageHelper from '@/utils/pageHelper';
import { handGetParent, handPostParent } from '@/utils/methodRequest';
import config from '@/config';

export const getList_PAGE = async (payload) => {
  const { data } = await axios.get('/page', {params: PageHelper.requestFormat(payload)});
  return PageHelper.responseFormat(data);
}
export const save_PAGE = async (payload) => {
  const { data } = await axios.post('/page', payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const update_PAGE = async (payload) => {
  const { data } = await axios.put('/page/' + payload.id, payload);
  if (data.message) config.notice.success(data.message);
  return data;
}
export const delete_PAGE = async (payload) => {
  for (let index = 0; index < payload.length; index++) {
    const id = payload[index];
    const {data} = await axios.delete('/page/' + id);
    if (data.message) config.notice.success(data.message);
  }
  return null;
}

export const get_MENU = async () => handGetParent("/menu/", "menus");
export const change_MENU = async (payload) => handPostParent('/menu/', payload);
export const delete_MENU = async (id) => {
  const { data } = await axios.delete('/menu/' + id);
  if (data.message) config.notice.success(data.message);
  return null;
}

export const get_SLIDE = async () => handGetParent("/slide/", "slides");
export const change_SLIDE = async (payload) => handPostParent('/slide/', payload);
export const delete_SLIDE = async (id) => {
  const { data } = await axios.delete('/slide/' + id);
  if (data.message) config.notice.success(data.message);
  return null;
}
