import modelEnhance from '@/utils/modelEnhance';
import PageHelper from '@/utils/pageHelper';

import {
  getList_PAGE,
  save_PAGE,
  update_PAGE,
  delete_PAGE,
  get_MENU,
  change_MENU,
  delete_MENU,
  get_SLIDE,
  change_SLIDE,
  delete_SLIDE,
} from "../service"

/**
* true when the page is first loaded
* Can use this value to prevent switching pages when
* Initialize data multiple times
*/
let url = true;
export default modelEnhance({
  namespace: 'page',

  state: {
    pageData: PageHelper.create(),
    menus: [],
    pages: [],
    slides: [],
  },

  subscriptions: {
    setup({ dispatch, history }) {
      history.listen(({ pathname, search }) => {
        if (pathname === '/page' && !!search && url) {
          url = false
          setTimeout(() => url = true, 500);
          const payload = PageHelper.URLToArray(search);
          dispatch({ type: 'init', payload });
        }
      });
    }
  },

  effects: {
    // Enter page load
    *init({ payload }, { put, select }) {
      const { pageData } = yield select(state => state.page);
      yield put({
        type: 'getPageInfo',
        payload: {
          pageData: pageData.setPage(payload)
        }
      });
      yield put({ type: 'getMenu' });
      yield put({ type: 'getSlide' });
    },
    // Get paging data
    *getPageInfo({ payload }, { call }) {
      const { pageData } = payload;
      const data = yield call (getList_PAGE, pageData)
      pageData.setPage(data);
    },
    // Save after querying paging
    *save({ payload }, { put, select, call }) {
      const { values, success } = payload;
      const { pageData } = yield select(state => state.page);

      if (values.id) yield call(update_PAGE, values);
      else yield call(save_PAGE, values);

      yield put({ type: 'getPageInfo', payload: { pageData } });
      yield put({ type: 'getMenu' });
      success();
    },
    // Delete after querying paging
    *remove({ payload }, { put, select, call }) {
      const { records, success } = payload;
      const { pageData } = yield select(state => state.page);
      const data = records.map(item => item.id)
      yield call(delete_PAGE, data);
      yield put({ type: 'getPageInfo', payload: { pageData } });
      yield put({ type: 'getMenu' });
      success();
    },

    *getMenu({ payload }, { call, put }) {
      const data = yield call(get_MENU)
      yield put({ type: 'success', payload: data });
    },
    *changeMenu({ payload }, { call, put }) {
      const menus = yield call(change_MENU, payload)
      yield put({ type: 'success', payload: { menus } });
    },
    *removeMenu({ payload }, { call, put }) {
      yield call(delete_MENU, payload);
      yield put({ type: 'getMenu' });
    },

    *getSlide({ payload }, { call, put }) {
      const data = yield call(get_SLIDE)
      yield put({ type: 'success', payload: data });
    },
    *changeSlide({ payload }, { call, put }) {
      const menus = yield call(change_SLIDE, payload)
      yield put({ type: 'success', payload: { menus } });
    },
    *removeSlide({ payload }, { call, put }) {
      yield call(delete_SLIDE, payload);
      yield put({ type: 'getSlide' });
    },
  },

  reducers: {
    success(state, { payload }) {
      return {
        ...state,
        ...payload,
      };
    },
  }
});
